import { Component, Inject, OnInit } from '@angular/core';
import { RestService } from '../../rest.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Doctors } from '../../doctors.model'; 
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-img-dialog',
  templateUrl: './img-dialog.component.html',
  styleUrls: ['./img-dialog.component.sass']
})
export class ImgDialogComponent implements OnInit {

  private fileTmp: any;
  fileUploadForm: FormGroup;
  doctors: Doctors;

  constructor(private restService: RestService, private fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: any,) {

    this.doctors = data.doctors;
    this.fileUploadForm = fb.group({
      singleUpload: [""],
      id: [this.doctors.id],
    });
  }

  ngOnInit(): void {
  }

  getFile($event: any) {
    const [file] = $event.target.files;
    console.log(file);
    this.fileTmp = {
      fileRaw: file,
      fileName: file.name
    }
  }

  sendFile() {
    const id = this.doctors.id;
    const body = new FormData();
    body.append('myFile', this.fileTmp.fileRaw, this.fileTmp.fileName);
    body.append('email', 'test@test.com');

    this.restService.sendPost(id, body)
      .subscribe(res => console.log(res))
  }


}
